* {
  box-sizing: border-box;
}
/* latin */
@font-face {
  font-family: 'Fira Code';
  font-style: normal;
  font-weight: 400;
  src: url(https://cdn.rawgit.com/tonsky/FiraCode/bbd33d9e/distr/woff2/FiraCode-Medium.woff2) format('woff2');
}
html,
body {
  border: 0;
  margin: 0;
  height: 100%;
  font-family: 'Segoe WPC', 'Segoe UI', 'SFUIText-Light', 'HelveticaNeue-Light', sans-serif;
  font-size: 14px;
}
ul,
li {
  display: block;
  margin: 0;
  padding: 0;
}
header h1 {
  font-size: 10pt;
  display: inline-block;
  margin: 1px 20px 1px 1px;
}
a {
  color: #d4d4d4;
  text-decoration: none;
}
a i.fa {
  opacity: 0.5;
  transition: opacity 0.2s;
}
a i.fa:hover {
  opacity: 1 !important;
}
header,
footer {
  display: block;
  padding: 2px 4px;
}
.flex-horizontal,
.flex-vertical {
  display: flex;
}
.flex-horizontal.dragging > :not(span),
.flex-vertical.dragging > :not(span) {
  pointer-events: none;
}
.flex-horizontal.dragging > span,
.flex-vertical.dragging > span {
  background: #666;
}
.flex-horizontal {
  flex-direction: row;
}
.flex-horizontal > span {
  display: block;
  width: 5px;
  cursor: col-resize;
}
.flex-vertical {
  flex-direction: column;
}
.flex-vertical > span {
  display: block;
  height: 5px;
  cursor: row-resize;
}
fieldset.vertical input[type=checkbox] {
  vertical-align: middle;
  min-width: 20px;
  margin-right: 43px;
}
fieldset.vertical > input,
fieldset.vertical > label {
  text-align: right;
  display: block;
}
fieldset.vertical > label > select,
fieldset.vertical > label > input {
  margin-left: 5px;
  min-width: 60px;
}
fieldset.vertical > input[type='submit'] {
  float: right;
}
fieldset.horizontal > input,
fieldset.horizontal > label {
  display: inline-block;
}
fieldset.horizontal > label > select,
fieldset.horizontal > label > input {
  margin-left: 5px;
}
fieldset button.link {
  display: block;
}
.flex-fill {
  flex-grow: 1;
}
.panelHeader {
  padding: 2px 4px;
  white-space: nowrap;
}
.panelHeader span {
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
}
.panelHeader a {
  float: right;
  margin-left: 5px;
}
#previewContainer {
  position: relative;
  overflow: hidden;
}
#previewFrame {
  position: absolute;
  margin: 0;
  border: 0;
  width: 100%;
  height: 100%;
  background: #eee;
}
#editorContainer {
  position: absolute;
  width: 100%;
  height: 100%;
}
.hover-show {
  display: none;
}
.hover:hover .hover-show {
  display: block;
}
.sourceNode {
  white-space: nowrap;
}
.sourceNode > div {
  display: block;
  transition: background-color 0.2s;
  padding-left: 5px;
}
.sourceNode > div:hover {
  background: #333;
}
.sourceNode > div > div {
  display: none;
  float: right;
  position: relative;
  z-index: 1;
}
.sourceNode > div > div i.fa {
  margin: 0 2px;
}
.sourceNode > div > a {
  display: block;
}
.sourceNode > div i.icon {
  margin-right: 4px;
}
.sourceNode.used > div i.icon {
  opacity: 1 !important;
}
.sourceNode > ul {
  padding-left: 10px;
}
.sourceNode:not(.open) > ul {
  display: none;
}
.sourceNode:not(.open) > div > a i.fa-folder-open-o,
.sourceNode.open > div > a i.fa-folder-o {
  display: none;
}
.sourceNode .inputWrapper {
  position: relative;
  padding-right: 33px;
  padding-left: 0px;
  display: inline-block;
  width: 100%;
}
.sourceNode .inputWrapper input {
  border: none;
  margin: 0;
  padding: 0;
  cursor: default;
  position: relative;
  width: 100%;
  background: none !important;
}
.sourceNode.new-file > div {
  display: flex;
}
.sourceNode.new-file > div input {
  box-sizing: border-box;
  flex-grow: 1;
  min-width: 0;
  padding: 2px;
  margin: -2px 0 -2px -2px;
}
ul.shaded .hover-show {
  display: none !important;
}
ul.shaded .sourceNode > div {
  background: none !important;
}
ul.shaded .sourceNode:not(.unshaded) > div {
  opacity: 0.25;
}
#fileContainer {
  overflow-x: hidden;
  overflow-y: auto;
}
footer > div > div {
  padding: 0 10px;
}
footer > div > div:first-child {
  padding-left: 0;
}
footer > div > div:last-child {
  padding-right: 0;
}
select {
  background: inherit;
  color: inherit;
}
select option {
  color: inherit;
}
input {
  border: 1px solid #888;
  border-radius: 3px;
  padding: 5px 7px;
}
.fa {
  position: relative;
}
.fa .fa-overlay {
  position: absolute;
  font-size: 50%;
  left: -0.4em;
  top: -0.3em;
  opacity: 1;
}
/*
*/
body.theme-vs,
body.theme-vs .dialog > * {
  background: #fffffe;
  color: #333;
}
body.theme-vs a {
  color: #2c2c2c;
}
body.theme-vs a > i.fa,
body.theme-vs button.link {
  color: #518cc8;
}
body.theme-vs header,
body.theme-vs footer,
body.theme-vs .flex-horizontal > span,
body.theme-vs .flex-vertical > span {
  background: #ccc;
}
body.theme-vs .sourceNode > div:hover,
body.theme-vs-dark select option {
  background: #E5EBF1;
}
body.theme-vs .sourceNode.selected > div {
  background: #ADD6FF;
}
body.theme-vs .panelHeader {
  background: #eee;
  border-bottom: 1px solid #ddd;
}
body.theme-vs .console-info {
  background: #FFFFC8;
}
body.theme-vs .console-warn {
  background: #EBF5FF;
}
body.theme-vs .console-error {
  background: #FFEBEB;
}
body.theme-vs .fa .fa-overlay {
  color: black;
  text-shadow: 1px 0px 2px #ddd, -1px 0px 2px #ddd, 0 1px 2px #ddd, 0 -1px 2px #ddd;
}
/*
*/
body.theme-vs-dark,
body.theme-vs-dark select,
body.theme-vs-dark input,
body.theme-vs-dark .dialog > * {
  background: #1e1e1e;
  color: #d4d4d4;
}
body.theme-vs-dark a {
  color: #d4d4d4;
}
body.theme-vs-dark a > i.fa,
body.theme-vs-dark button.link {
  color: #8bb2da;
}
body.theme-vs-dark header,
body.theme-vs-dark footer,
body.theme-vs-dark .flex-horizontal > span,
body.theme-vs-dark .flex-vertical > span {
  background: #333;
}
body.theme-vs-dark .sourceNode > div:hover,
body.theme-vs-dark select option {
  background: #3A3D41;
}
body.theme-vs-dark .sourceNode.selected > div {
  background: #264F78;
}
body.theme-vs-dark .panelHeader {
  background: #444;
  border-bottom: 1px solid #444;
}
body.theme-vs-dark .console-info {
  background: #383800;
}
body.theme-vs-dark .console-warn {
  background: #000a14;
}
body.theme-vs-dark .console-error {
  background: #140000;
}
body.theme-vs-dark .fa .fa-overlay {
  color: #ffc;
  text-shadow: 1px 0px 2px #1e1e1e, -1px 0px 2px #1e1e1e, 0 1px 2px #1e1e1e, 0 -1px 2px #1e1e1e;
}
/*
*/
body.theme-hc-black,
body.theme-hc-black .dialog > * {
  background: black;
  color: white;
}
body.theme-hc-black a {
  color: #ccc;
}
body.theme-hc-black a > i.fa,
body.theme-hc-black button.link {
  color: #cce5ff;
}
body.theme-hc-black .flex-horizontal > span,
body.theme-hc-black .flex-vertical > span {
  background: #888;
}
body.theme-hc-black .sourceNode > div:hover {
  background: white;
  color: black;
}
body.theme-hc-black select option {
  background: black;
  color: white;
}
body.theme-hc-black .sourceNode.selected > div {
  background: white;
  color: black;
}
body.theme-hc-black .panelHeader {
  background: #111;
  border-bottom: 1px solid #111;
}
body.theme-hc-black .fa .fa-overlay {
  color: yellow;
  text-shadow: 1px 0px 2px black, -1px 0px 2px black, 0 1px 2px black, 0 -1px 2px black;
}
body:not(.preview-docked) .preview-docked-show,
body:not(.console-visible) .console-visible-show,
body.preview-paused #btnPause,
body:not(.authenticated) .authenticated-show,
body:not(.preview-paused) #btnRun {
  display: none;
}
#consoleContainer {
  font-family: 'Fira Code';
  overflow-y: scroll;
}
#consoleContainer div.error {
  color: red !important;
}
#consoleContainer div.error i {
  float: right;
}
.console-error {
  color: red;
}
.dialog {
  position: absolute;
  display: none;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.25);
  padding-left: 100px;
  padding-top: 100px;
}
.dialog > * {
  max-width: 90%;
  border: 1px solid #888;
  border-radius: 4px;
  box-shadow: 1px 5px 30px rgba(0, 0, 0, 0.75);
}
.dialog fieldset {
  display: inline-block;
  padding: 10px;
}
fieldset.vertical > input,
fieldset.vertical > label {
  display: block;
}
fieldset.vertical > input[type='submit'] {
  float: right;
}
fieldset.horizontal > input,
fieldset.horizontal > label {
  display: inline-block;
}
fieldset button.link {
  display: block;
}
button.link,
button.link:focus,
button.link:active {
  /* Remove all decorations to look like normal text */
  background: none;
  border: none;
  display: inline;
  font: inherit;
  margin: 0;
  padding: 0;
  outline: none;
  outline-offset: 0;
  /* Additional styles to look like a link */
  color: inherit;
  cursor: pointer;
}
button.link:hover {
  text-decoration: underline;
}
/* Remove extra space inside buttons in Firefox */
button.link::-moz-focus-inner {
  border: none;
  padding: 0;
}
